@import url('https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap" rel="stylesheet');

.wrapper{
    /* background-image: url('../../img/Фон\ бегун.jpg'); */
    background-image: url('../../img/fonLogin.jpg');
    background-size: cover; 
    width: 100%; 
    height: 100%;  
    position: fixed; 

}

.loginPage{
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
    margin-top: 50px;
}

.quote {
    background: #eee;
    opacity: 0.5;
    padding: 1em;
    border-radius: 1em;
    position: absolute; 
    right: 0;
    /* font-family: 'Montserrat'; */
    font-family: var(--montserrat-font);
    letter-spacing: 0.8px;
    width: 300px;
  }
  .quote figcaption,
  .quote blockquote {
    margin: 0.5em;
  }

.description{
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #fff;
  margin: 20px 40px;
  top: 120px;
  font-family: 'Rubik';
}

.titleOne{
  color: #fff;
  font-family: 'Montserrat';
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;

  @media(max-width: 960px){
    font-size: 16px;
  }

  @media(max-width: 400px){
    font-size: 12px;
  }

  
  @media(max-width: 270px){
    font-size: 8px;
  }

}

.line{
  width: 8%; 
  height: 3px; 
  margin: 0 30px;
  background:#ffffff;

  
  @media(max-width: 610px){
    display: none;
  }
}

.title{
  font-size: 80px;
  color: #fff;
  font-family: 'DelaGothicOne';
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;

  @media(max-width: 960px){
    font-size: 55px;
  }

  @media(max-width: 710px){
    font-size: 45px;
  }

  @media(max-width: 610px){
    font-size: 40px;
  }

  @media(max-width: 610px){
    font-size: 30px;
  }

  @media(max-width: 270px){
    font-size: 22px;
  }

}


.subscribe{
  color: #fff;
  font-family: 'DelaGothicOne';
  text-transform: uppercase;
  color: #f3a140;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  margin: 0px;

  @media(max-width: 400px){
    font-size: 20px;
  }

  @media(max-width: 270px){
    font-size: 15px;
  }
}

.loginBlock{
  display: flex;
  justify-content: center;
  margin: 20px;
}